* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  justify-content: center;
  align-items: center;
  padding: 30px;
  display: flex;
}

a, span {
  display: flex;
}

.people {
  grid-template-columns: repeat(2, 1fr);
  justify-content: space-between;
  align-items: center;
  gap: 40px 30px;
  max-width: 890px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
  }
}

.person {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  transition: transform 1s, border-radius 1s;
  display: flex;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 84px;
  left: 158px;
}

.person:hover {
  background-color: #eee;
  border-radius: 12px;
  transform: scale(1.2);
}

.photo {
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin-bottom: 8px;
  position: relative;
}

